/* 登录验证 */
export default {
  methods: {
    // 登录验证
    loginConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '请先登录',
          confirmButtonText: '登录',
          cancelButtonText: '返回'
        }).then(() => {
          // 前往登录页面
          this.$router.replace({
            path: '/login',
            query: {
              backUrl: this.$route.fullPath
            }
          })
        }).catch(() => {})
        return true
      }
      return false
    }
  }
}
